.gallery {
  grid-gap: 16px;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin-bottom: 20px;
  display: grid;
}

.photo-card {
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.info {
  justify-content: space-around;
  padding: 10px;
  display: flex;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #3f51b5;
  border: none;
  border-radius: 5px;
  margin: 0 auto;
  padding: 10px 20px;
  display: block;
}

